export default {
	data() {
		return {
			form: {
				APUId: '', // 采购人
				APUName: '', //采购人姓名
				ASId: '', // 供应商标识
				ASName: '', // 供应商姓名
				AGId: '', // 采购物品
				AGName: '', // 物品名称
				Price: '', // 采购价格
				Count: '', // 采购量
				IsPay: false, // 已付 运费 和 货款
				IsPayFreight: false, // 已付运费
				IsPFG: false, // 已付货款
				IsNoBatch: false // 不记批次
			},
			index: 0,
			array: ['中国', '美国', '巴西', '日本'],
			rules: {
				AUName: {
					required: '',
					message: '请输入姓名'
				},
				AUPhone: {
					required: '',
					type: 'phone',
					message: '请输入手机号'
				},
				AUIdCard: {
					required: '',
					type: 'cardnum',
					message: '请输入身份证'
				}
			}
		};
	},
	methods: {
		submitForm: function(param) {
			uni.showModal({
				content: '表单数据内容：' + JSON.stringify(param),
				showCancel: false
			});
		},
		bindPickerChange(e) {
			this.index = e.target.value;
		}
	}
};
